.top-container {
  display: flex;
  align-items: center;
  background-color: #3367d6;
  padding: 15px 30px;
  padding-left: 70px;
}
.search-bar {
  border: 1px solid #ececec;
  border-radius: 3px;
  background-color: #2850a7;
  padding: 10px;
  margin-left: 30vw;
  display: flex;
  align-items: center;
  width: 30vw;
}
.search-bar img {
  margin: 0px 10px;
}
.search-bar input {
  border: none;
  outline: none;
  background: none;
  flex: 1;
  margin-left: 10px;
}
.search-input {
  color: white;
}
.body-container {
  margin: 50px;
}
.cards-container {
  box-shadow: 0px 4px 16px 0px #bfbfbf;
  padding: 20px;
  border-radius: 10px;
}
ul {
  display: grid;
  gap: 30px;
}
.empty {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: gray;
  font-size: 22px;
}
