li {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.list-container {
  display: flex;
  align-items: center;
}
.time {
  font-size: 17px;
  font-weight: bold;
  color: #475569;
  margin-right: 50px;
}
.logo {
  width: 30px;
  height: 30px;
}
.title {
  font-size: 16px;
  font-weight: bold;
  color: #475569;
  margin-left: 20px;
  margin-right: 10px;
}
.url {
  font-size: 15px;
  color: #64748b;
}
.delete {
  border: none;
  outline: none;
  background: none;
}
.delete img {
  width: 25px;
}
